.Ap_mainDiv{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    font-size: 30px;
    background-color: #0f0f0f;
}

.Ap_ceoLink{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
    color: rgb(184, 197, 255);
}

.Ap_ButtonContainer{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 20px;
    padding-bottom: 10px;
}

.Ap_Button{
    width: 110px;
    height: 110px;
    font-size: 28px;
    border-radius: 75px;
    margin-left: 20px;
    cursor: pointer;
    background-color: #0f0f0f;
    color: white;
    font-weight: 550;
}

.Ap_Button:hover{
    box-shadow: 0px 0px 10px blue;
    border-color: blue;
}