.SI_mainDiv{
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #0f0f0f;
    padding: 20px;
    align-items: center;
}

.SI_formContainer{
    display: grid;
    grid-template-columns: 50% 50%;
    width: 60%;
    grid-gap: 20px 20px;
}

.SI_Button{
    width: 110px;
    height: 110px;
    font-size: 28px;
    border-radius: 75px;
    margin-left: 20px;
    cursor: pointer;
    background-color: #0f0f0f;
    color: white;
    font-weight: 550;
}

.SI_Button:hover{
    box-shadow: 0px 0px 10px blue;
    border-color: blue;
}