.Audit_mainDiv{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    background-color: #0f0f0f;
    padding-bottom: 10px;
    padding-top: 10px;
}

.Audit_content{
    display: flex;
    flex-direction: column;
    width: fit-content;
    font-size: 30px;
    color: rgb(184, 197, 255);
    align-items: center;
    padding: 20px;
    border: 1px solid rgb(31, 31, 31);
    border-radius: 20px;
    margin-top: 10px;
}

.Audit_content div{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: flex-start;
}

.Audit_logoContainer{
    display: flex;
    align-items: center;
    font-size: 48px;
    color: rgb(59, 104, 255);
}