.AddCoinSlider_Main{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: rgb(24, 24, 24);
    padding-top: 50px;
}

.AddCoinSlider_Container{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 95%;
    max-height: 500px;
}

.Slider_Container{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
}

.Slider_Steps{
    position: absolute;
    z-index: 3;
    display: grid;
    grid-template-columns: repeat(7, 14.28%);
    width: 100%;
}

.Step_container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Step_Dot{
    width: 40px;
    height: 40px;
    background-color: rgb(0, 0, 0);
    border-radius: 20px;
    cursor: pointer;
    box-shadow: 0px 0px 10px blue;
    z-index: 0;
    transition: all 0.5s ease-out;
}

.Step_Dot:hover{
    box-shadow: 0px 0px 10px rgb(152, 152, 255);
}

.Step_Text{
    font-size: 30px;
    color: white;
}

.Slider_Progress{
    position: relative;
    width: 85.72%;
    height: 10px;
    background-color: rgb(0, 0, 0);
}

.Slider_Progress_bar{
    position: absolute;
    height: 100%;
    background-color: blue;
    top: 0;
    left: 0;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    z-index: 2;
    transition-property: width;
    transition-duration: 0.5s;
    transition-timing-function: ease-out;
    box-shadow: 0px 0px 10px blue;
}

.Slider_Steps_Text{
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(7, 14.28%);
    width: 95%;
}

.Forms{
    width: 100%;
    height: fit-content;
    margin-top: 60px;
    border-top: 1px solid rgb(32, 32, 32);
    background-color: rgb(15, 15, 15);
}

.nextStepButton{
    width: 100px;
    height: 100px;
    border-radius: 50px;
    cursor: pointer;
    font-size: 30px;
    background-color: black;
    color: blue;
}

.nextStepButton:hover{
    text-shadow: 0px 0px 10px blue;
}

.CheckBoxes{
    width: 30px;
    height: 30px;
    border-radius: 15px;
    margin-bottom: 0px;
}
