.TI_mainDiv{
    width: 100%;
    display: flex;
    background-color: #0f0f0f;
    align-items: center;
}

.TI_Logo{
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 30px;
    width: 300px;
    height: 300px;
    background-color: black;
    border-radius: 20px;
    cursor: pointer;
    box-shadow: 0px 0px 10px black;
    background-size: cover;
}

.TI_Logo:hover{
    box-shadow: 0px 0px 10px white;
}

.TI_Fields{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin-left: 20px;
    font-size: 1px;
}

.TI_Button{
    width: 150px;
    height: 150px;
    font-size: 30px;
    border-radius: 75px;
    margin-left: 20px;
    cursor: pointer;
    background-color: #0f0f0f;
    color: white;
    font-weight: 550;
}

.TI_Button:hover{
    box-shadow: 0px 0px 10px blue;
    border-color: blue;
}