.Nofitication{
    z-index: 10000000;
    position: absolute;
    background-color: rgb(0, 0, 0);
    border-radius: 15px;
    box-shadow: 0px 0px 25px rgb(0, 0, 0);
    color: white;
    padding : 15px;
    display: flex;
    flex-direction: column;
    transform : translate(-50%, 0)
}

.NotifHeader{
    display: flex;
    
    margin-bottom: 5px;
}

.NotifHeader_text{
    margin-bottom: 15px;
    margin-left: 8px;
}

.NotifButtons{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: flex-start;
    align-self: baseline;
    padding: 5px;
    height: 100%;
}

.NotifButton{
    margin-right: auto;
    margin-left: auto;
    height: 30px;
    width: 100px;
    background-color: rgb(0, 0, 0);
    border-style: none;
    padding: 5px;
    border-radius: 8px;
    color: rgb(238, 255, 0);
    border: 1px solid rgb(238, 255, 0);
    align-self: center;
}

.NotifButton:hover{
    cursor: pointer;
    box-shadow: 0px 0px 18px rgb(200, 255, 0);
    transition: all 0.2s ease-in-out;
}