.Pay_mainDiv{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 20px;
    align-items: center;
    background-color: #0f0f0f;
}

.Pay_title{
    font-size: 40px;
    color: blue;
}

.Pay_details{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Pay_details span{
    font-size: 35px;
    color: white;
    margin-top: 20px;
}

.Pay_Button{
    width: 120px;
    height: 120px;
    font-size: 28px;
    border-radius: 75px;
    margin-left: 20px;
    cursor: pointer;
    background-color: #0f0f0f;
    color: white;
    font-weight: 550;
    padding: 10px;
}

.Pay_Button:hover{
    box-shadow: 0px 0px 10px blue;
    border-color: blue;
}