.P_mainDiv{
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #0f0f0f;
    padding: 20px;
}

.P_formContainer{
    display: flex;
    justify-content: space-evenly;
}

.P_formContainer div{
    display: flex;
    flex-direction: column;
    padding: 10px;
    border: 1px solid white;
    align-items: center;
    width: 400px;
    border-radius: 20px;
}

.P_formContainer div span{
    color: white;
}



.P_planTitle{
    width: fit-content;
    font-size: 55px;
    font-family: fantasy;
}

.P_planContent{
    margin: 20px 0px;
    border: none;
}

.P_planText{
    width: 100%;
    font-size: 30px;
}

.P_planPrice{
    padding: 0px;
    border: none;
    flex-direction: row;
    width: 100%;
    font-size: 40px;
    margin-top: auto;
}

.P_Buttons {
    width: 110px;
    height: 110px;
    font-size: 30px;
    border-radius: 75px;
    margin-left: 20px;
    cursor: pointer;
    background-color: #0f0f0f;
    color: white;
    font-weight: 550;
    justify-content: center;
}

.P_Buttons:hover{
    box-shadow: 0px 0px 10px blue;
    border-color: blue;
}

.P_planButton{
    width: 100%;
    height: fit-content;
    font-size: 30px;
    margin-top: 5px;
    padding: 13px;
    background-color: #0f0f0f;
    border-radius: 20px;
    cursor: pointer;
}

.P_planButton:hover{
    box-shadow: 0px 0px 10px white;
}