.EI_mainDiv{
    background-color: #0f0f0f;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #0f0f0f;
    padding: 20px;
}

.EI_mainDiv input{
    margin-bottom: 20px;
}

.EI_mainDiv span{
    font-size: 30px;
    color: rgb(255, 255, 255);
    margin-left: 10px;
}

.EI_BoxexContainer{
    display: flex;
    width: 100%;
    align-items: center;
    padding: 20px;
    justify-content: space-evenly;
}

.EI_checkBox{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.EI_checkBox span{
    font-size: 30px;
    margin-bottom: 10px;
}

.EI_checkBox input{
    width: 30px;
    height: 30px;
    border-radius: 15px;
    cursor: pointer;
}

.EI_box{
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid #323232;
    border-radius: 10px;
    height: 500px;
    width: fit-content;
    margin: 30px;
}

.EI_EventSection{
    pointer-events: none;
    opacity: 0.4;
}

.EI_timeInput{
    display: flex;
    flex-direction: column;
}

.EI_timeInput span{
    font-size: 30px;
    color: rgb(184, 197, 255);
    margin-bottom: 10px;
}

.EI_smallInputs{
    display: flex;
    align-items: center;
}

.EI_smallInputs input{
    margin-right: 10px;
    width: 130px;
}

.EI_SliderText{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 480px;
    justify-content: space-between;
    margin-left: auto;
    font-size: 25px;
    color: #6c6c6c;
    margin-right: 18px;
}

.EI_SliderText span{
    color: #6c6c6c;
}

.EI_SliderContainer{
    position: relative;
    display: flex;
    flex-direction: column;
    height: 500px;
    width: 88px;
    align-items: center;
    justify-content: center;
    margin-right: 40px;
}

.EI_SliderDot{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
}

.EI_SliderDot span{
    border: 4px solid black;
    width: 40px;
    height: 40px;
    border-radius: 100px;
    background-color: black;
}

.EI_SliderLine{
    background-color: black;
    width: 6px;
    height: 96%;
}

.EI_Bottom{
    display: flex;
    height: fit-content;
    margin-top: 10px;
}

.EI_Bottom_Phase{
    display: flex;
    flex-direction: column;
    margin-right: 20px;
}

.EI_Bottom_Buttons{
    display: flex;
    margin-left: auto;
    align-items: center;
}

.EI_TextArea{
    width: 912px;
    height: 200px;
    resize: none;
}

.EI_Button{
    width: 150px;
    height: 150px;
    font-size: 30px;
    border-radius: 75px;
    margin-left: 20px;
    cursor: pointer;
    background-color: #0f0f0f;
    color: white;
    font-weight: 550;
}

.EI_Button:hover{
    box-shadow: 0px 0px 10px blue;
    border-color: blue;
}