.PI_mainDiv{
    width: 100%;
    display: flex;
    background-color: #0f0f0f;
    align-items: center;
}

.PI_formContainer{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding:20px;
}

.PI_textArea {
    resize: none;
    width: 912px;
    height: 200px;
}

.PI_Buttons {
    width: 110px;
    height: 110px;
    font-size: 28px;
    border-radius: 75px;
    margin-left: 20px;
    cursor: pointer;
    background-color: #0f0f0f;
    color: white;
    font-weight: 550;
}

.PI_Buttons:hover{
    box-shadow: 0px 0px 10px blue;
    border-color: blue;
}