.footer_mainDiv{
    display: flex;
    flex-direction: column;
    margin-top: auto;
    color: white;
    font-size: 35px;
    width: 100%;
    background-color: #0f0f0f;
    padding: 20px 0px;
    align-items: center;
    max-width: 2100px;
}

.footer_links{
    display: flex;
    margin-bottom: 20px;
    font-size: 28px;
    color: rgb(201, 201, 201);
}

.footer_rights{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    font-size: 22px;
}

.footer_linksContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer_linksContainer a{
    color: white;
}

.footer_linksContainer a:hover{
    color: blue;
}