*{
    box-sizing: border-box;
}

select{
    font-size: 25px;
    background-color: #0f0f0f;
    padding: 15px;
    border-radius: 15px;
    color: white;
    margin-right: auto;
    border-color: #1f1f1f;
    outline-style: none;
}

select:hover{
    box-shadow: 0px 0px 10px blue;
    cursor: pointer;
}

input{
    padding: 10px;
    font-size: 25px;
    border-radius: 20px;
    text-indent: 10px;
    background-color: #0f0f0f;
    border: 1px solid rgb(31, 31, 31);
    color: white;
    outline-style: none;
}

textarea{
    padding: 10px;
    font-size: 25px;
    border-radius: 20px;
    text-indent: 10px;
    background-color: #0f0f0f;
    border: 1px solid rgb(31, 31, 31);
    color: white;
    outline: none;
}

.SliderDot{
    display: flex;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    position: absolute;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
}

.SliderDot span{
    border: 5px solid black;
    background-color: black;
}

.SliderLine{
    background-color: black;
    width: 5px;
    height: 100%;
    margin: 10px 0px;
}