.Lo_mainDiv{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: fit-content;
    align-items: center;
    background-color: #0f0f0f;
    padding-top: 20px;
}

.Lo_TreasureLogo{
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Lo_TreasureLogo span{
    font-size: 35px;
    color: white;
}

.Lo_participationDetails{
    display: flex;
    flex-direction: column;
    color: rgb(184, 197, 255);
    font-size: 30px;
    align-items: center;
    margin-top: 20px;
}

.Lo_check{
    display: flex;
    align-items: center;
    margin: 20px 0px;
}

.Lo_Button{
    padding: 10px;
    font-size: 28px;
    border-radius: 20px;
    background-color: #0f0f0f;
    color: white;
    margin-left: 10px;
    border-color: rgb(59, 104, 255);
    width: 100%;
}

.Lo_Button:hover{
    cursor: pointer;
    border-color: blue;
    box-shadow: 0px 0px 10px blue;
}

.Lo_Cancel{
    margin-bottom: 10px;
}

.Lo_Timer{
    display: flex;
    flex-direction: column;
    width: 161px;
    max-width: 300px;
    align-items: center;
    font-size: 30px;
    color: white;
    border: 1px solid blue;
    box-shadow: 0px 0px 10px blue;
    padding: 10px;
    border-radius: 20px;
    margin-bottom: 20px;
}

.Lo_winner{
    display: flex;
    flex-direction: column;
    font-size: 30px;
    color: white;
    padding: 10px;
    border-radius: 20px;
    align-items: center;
}