.NFT_mainDiv{
    display: flex;
    flex-direction: column;
    font-size: 35px;
    color: rgb(184, 197, 255);
    background-color: #0f0f0f;
    align-items: center;
    padding-bottom: 20px;
}

.NFT_developmentLogo{
    width: fit-content;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #1f1f1f;
    padding: 20px;
    border-radius: 30px;
}