.header {
  display: flex;
  flex-direction: row;
  min-height: 175px;
  background-color: #0c0c0c;
  width: 100%;
  max-width: 3000px;
  align-items: center;
  padding: 20px;
  color: white;
  min-width: 400px;
}

.header span{
  white-space: nowrap;
}

.header_nav{
  font-size:20px;
  color:white;
  background-color:#0c0c0c;
  border:none;
  border-radius:0px;
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
  margin-right:40px;
}

.header_nav:hover{
  color: blueviolet;
}

.header_menu_phone{
  color: blue;
}
