.TD_mainDiv{
    display: flex;
    flex-direction: column;
    padding: 20px 20px;
    border: 1px solid #1f1f1f;
    margin-left: 20px;
}

.TD_ChainLogo{
    display: flex;
    align-items: center;
    width: 100px;
}

.TD_projectDetails{
    font-size: 25px;
    border: 1px solid #1f1f1f;
    margin-top: 20px;
    padding: 10px;
    overflow-y: scroll;
    height: 175px;
}

.TD_tokenomicsContainer{
    display: flex;
    width: 100%;
    margin-top: 20px;
    position: relative;
    border: 1px solid rgb(31, 31, 31);
    background-color: #1a1a1a;
    border-radius: 10px;
}

.TD_tokenomicsText{
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px;
    display: flex;
    flex-direction: column;
}


.TD_tokenomicsTitle{
    font-size: 35px;
    color: #3b68ff;
    margin-bottom: 10px;
}

.TD_tokenomicsPercent{
    font-size: 23px;
    color: rgb(184, 197, 255);
    margin-bottom: 5px;
}

.TD_pieChartContainer{
    display: flex;
    flex-direction: column;
    height: 275px;
    width: 70%;
}

.TD_TETContainer{
    display: flex;
    width: 100%;
    margin-top: 20px;
    height: 327px;
}

.TD_TET_TokenEvent{
    display: flex;
    flex-direction: column;
    width: 82%;
    font-size: 22px;
}

.TD_TET_TokenEvent_Token{
    display: flex;
    flex-direction: column;
    width: 94%;
    height: fit-content;
    padding: 10px;
    border: 1px solid rgb(31, 31, 31);
    font-size: 22px;
}

.TD_TET_TokenEvent_Token :nth-child(odd){
    color: rgb(59, 104, 255);
    font-size: 25px;
}

.TD_TET_TokenEvent_Token :nth-child(even){
    margin-bottom: 5px;
}

.TD_TET_TokenEvent_Event{
    display: grid;
    grid-template-columns: repeat(3, 33.33333%);
    margin-top: 10px;
    grid-gap: 5px 5px;
}

.TD_TET_TokenEvent_Event_Box{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 23px;
    padding: 5px 0px;
    border: 1px solid rgb(31, 31, 31);
}

.TD_TimeLine_Texts{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 10%;
    height: 100%;
    justify-content: space-between;
    padding: 10px 0px;
    font-size: 23px;
    margin-right: 10px;
    color: rgb(184, 197, 255);
}

.TD_Timeline_Container{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 8%;
    height: 100%;
}

.TD_TimeLine_Line{
    width: 4px;
    height: 100%;
    background-color: black;
    box-shadow: 0px 0px 10px blue;
    margin: 5px 0px;
}

.TD_EventDetails{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    border: 1px solid rgb(31, 31, 31);
    padding: 10px;
    height: 175px;
    overflow-y: scroll;
}

.TD_EventDetails span:nth-child(1){
    font-size: 30px;
    color: rgb(59, 104, 255);
    margin-bottom: 5px;
}

.TD_EventDetails span:nth-child(2){
    font-size: 23px;
    color: rgb(184, 197, 255);
}

.TD_ShortAudit{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    padding: 20px;
    border: 1px solid rgb(31, 31, 31);
    background-color: #1a1a1a;
}

.TD_ShortAudit_Title{
    font-size: 30px;
    color: rgb(59, 104, 255);
    margin-bottom: 10px;
    font-family: fantasy;
    border-bottom: 1px solid rgb(31, 31, 31);
    padding-bottom: 10px;
}

.TD_ShortAudit div{
    font-size: 25px;
    margin-bottom: 5px;
}

.TD_Logo{
    width: 220px;
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border: 1px solid #1f1f1f;
    border-top-right-radius: 15px;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
}

.TD_social{
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-row-gap: 5px;
    grid-column-gap: 5px;
    font-size: 25px;
    margin-top: 10px;
    cursor: pointer;
}

.TD_social div{
    border: 1px solid rgb(31, 31, 31);
    padding: 10px;
    display: flex;
    justify-content: center;
    border-radius: 10px;
    color: white;
}

.TD_social div:hover{
    border-color: blue;
    box-shadow: 0px 0px 10px blue;
}

.TD_social div a{
    text-decoration: none;
    color: white;
}

.TD_timeline_dotDiv{
    position :absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
    justify-content: space-between;
}

.TD_timeline_dotDiv span{
    width: 40px;
    height: 40px;
    border-radius: 30px;
    background-color: black;
    box-shadow: 0px 0px 10px blue;
    border: 2px solid black;
}

.TD_timeline_dotDiv span:hover{
    box-shadow: 0px 0px 10px rgb(155, 155, 255);
    cursor: pointer;
    transition: all 0.5s ease;
}

.TD_eventLink{
    font-size: 25px;
    background-color: #0f0f0f;
    color: rgb(184, 197, 255);
    border: 1px solid rgb(31, 31, 31);
}
.TD_eventLink:hover{
    border-color: blue;
    box-shadow: 0px 0px 10px blue;
    cursor: pointer;
}

.TD_utility button{
    font-size: 25px;
    padding: 10px;
    cursor: pointer;
    background-color: #0f0f0f;
    color: white;
    border-color: rgb(31, 31, 31);
    width: 150px;
}

.TD_auditKYCChain{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: max-content;
    margin-left: auto;
}

.TD_auditKYCChain span{
    margin-right: 10px;
    font-size: 25px;
    border: 1px solid rgb(31, 31, 31);
    padding: 20px;
    border-radius: 15px;
}

.TD_auditKYCChain span:hover{
    box-shadow: 0px 0px 10px blue;
    border-color: blue;
    cursor: pointer;
    border-radius: 15px;
}

a {
    text-decoration: none;
}

.TD_watchList{
    display: flex;
    align-items: center;
    font-size: 25px;
    padding: 5px;
    border: 1px solid #1f1f1f;
    margin-bottom: 5px;
    border-radius: 15px;
    color: gold;
    width: 158px;
}

.TD_watchList:hover{
    cursor: pointer;
    box-shadow: 0px 0px 10px gold;
    border-color: gold;
}

.TD_watchListCount{
    border: 1px solid rgb(31, 31, 31);
    margin-top: 5px;
    padding: 8px;
    border-radius: 15px;
    width: fit-content;
    color: rgb(184, 197, 255);
}


.TD_tokenInfo_m{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: fit-content;
    padding: 10px;
    border: 1px solid rgb(31, 31, 31);
    font-size: 75px;
}

.TD_tokenInfo_m :nth-child(odd){
    color: rgb(59, 104, 255);
    font-size: 75px;
}

.TD_tokenInfo_m :nth-child(even){
    margin-bottom: 25px;
    color: rgb(194, 194, 194);
}