.coinList{
    width: 100%;
    background-color: rgb(15, 15, 15);
    color: white;
    display: block;
    box-sizing: border-box;
    padding: 15px;
}

.coins{
    display: grid;
    grid-template-columns: 100%;
    grid-row-gap: 10px;
    background-color: rgb(15, 15, 15);
}

.coinsList{
  display: grid;
  grid-template-columns: 53% 47%;
}

.CoinList_Header{
    display: flex;
    align-items: center;
    padding: 15px;
}

.searchInput{
  font-size: 25px;
  background-color: #0f0f0f;
  border: 1px solid #1a1a1a;
  padding: 15px;
  border-radius: 15px;
  color: white;
}

.rowDiv{
  height: fit-content;
}

.CL_TokensTable{
  overflow-y: scroll;
  padding: 20px;
}

::-webkit-scrollbar{
  background-color: rgb(12, 12, 12);
  width: 20px;
}

::-webkit-scrollbar-thumb {
	background-color: blue;
}

::-webkit-scrollbar-corner{
  background-color: rgb(12, 12, 12);
}

.CL_td_mobile{
  overflow: hidden;
  max-height: 0px;
  transition-property: max-height;
  transition-timing-function: ease-in-out;
  transition-duration: 0.2s;
}