.Body {
  display: flex;
  flex-direction: column;
  max-width: 3000px;
  min-width: 1900px;
  width: 100%;
  background-color: #0f0f0f;
  align-items: center;
  height: fit-content;
  position: relative;
  z-index: 3;
}

.Body_List_Part{
  width: 100%;
}

.Body_Headers{
  display: grid;
  grid-template-columns: repeat(5, 20%);
  font-size: 40px;
  background-color: #0c0c0c;
  color: white;
  width: 100%;
  border-bottom: 1px solid black;
  box-sizing: border-box;
}

.Body_Headers div{
  display: flex;
  align-items: center;
  height: 64px;
  justify-content: center;
  color: rgb(255, 255, 255);
  padding: 15px 0px;
  cursor: pointer;
  font-size: 30px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.Body_Headers div:hover{
  color: rgb(123, 123, 255);
  transition: all 0.2s ease;
}

.Body_Header_selected_Effect{
  text-shadow: 0px 0px 10px rgb(255, 255, 255);
}

.Body_promoted{
  display: grid;
  grid-template-columns: repeat(5, 20%);
  width: 100%;
  align-items: center;
  color: white;
  font-size: 25px;
  border: 1px solid #1f1f1f;
  padding: 10px;
  border-radius: 20px;
  background-color: #1a1a1a;
}

.Body_promotedsContainer{
  display: flex;
  flex-direction: column;
  padding: 20px;
  align-items: center;
}

.Body_promotedTitle{
  font-size: 30px;
  color: white;
  text-shadow: 0px 0px 10px white;
  margin-bottom: 10px;
}

.Body_Menu_phone{
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  height: 100%;
  width: 100%;
  transition-property: opacity;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  background-color: #1a1a1a;
  color: white;
  font-size: 55px;
  z-index: -2;
}

.Body_Menu_list{
  list-style: none;
}

.Body_Menu_list li{
  display: flex;
  align-items: center;
  margin: 20px 0px;
  gap: 20px;
}

.Body_menu_row{
  font-size: 85px;
  white-space: nowrap;
}