.Coin{
    width: 100%;
    height: 155px;
    background-color: rgb(26, 26, 26);
    display: grid;
    grid-template-columns: 13% 60% 27%;
    position: relative;
    align-items: center;
    margin-bottom: 20px;
}

.Coin:hover{
    cursor: pointer;
    box-shadow: 0px 0px 5px white;
}

.CL_Logo{
    width: 155px;
    height: 155px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border: 1px solid #313131;
}

.CL_Row_Info{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    justify-content: space-evenly;
}

.CL_coinInfo{
    display: flex;
}

.CoinRow_event{
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    padding: 0px 10px;
    width: 100%;
}

.CoinRow_event span:nth-child(1){
    font-size: 35px;
    margin-bottom: 5px;
    color: white;
}

.CoinRow_event span:nth-child(2){
    font-size: 25px;
    color: rgb(204, 204, 204);
}

.Audit_KYC_abs{
    position: absolute;
    top: 0%;
    right: 2%;
    width: 80px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    font-size: 30px;
    align-items: center;
}

.Audit_KYC_abs div{
    width: 90px;
    padding: 5px;
    border: 1px solid #353535;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    padding: 10px;
}


.CR_Category{
    border: 1px solid #353535;
    width: fit-content;
    border-radius: 10px;
    padding: 5px;
    color: rgb(184, 197, 255);
    margin-left: 5px;
    font-size: 25px;
}

.CR_Name{
    font-size: 33px;
}

.CR_Symbol{
    font-size: 25px;
    color: #898787;
    margin-left: 10px;
}

.CR_chainLogo{
    display: flex;
    align-items: center;
    width: 40px;
    height: 50px;
    margin-left: 10px;
}

.CR_Chain{
    font-size: 32px;
}

.CR_star:hover{
    color:'blue';
}